import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  OnInit,
  OnDestroy,
  Optional
} from '@angular/core';
import { DashboardBridgeService } from './dashboard-bridge.service';
import { WidgetsDashboardComponent } from '@c8y/ngx-components';
import { Subscription } from 'rxjs';

@Component({
  selector: 'c8y-widget-legacy',
  template: '',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class WidgetComponent implements OnInit, OnDestroy {
  ng1Scope;
  config;
  private resizeSubscription?: Subscription;
  constructor(
    @Optional() private dashboard: WidgetsDashboardComponent,
    private dashboardBridgeService: DashboardBridgeService,
    private el: ElementRef
  ) {}

  async ngOnInit() {
    if (this.config) {
      this.ng1Scope = await this.dashboardBridgeService.instantiateComponent(
        this.config,
        this.el.nativeElement
      );
      if (this.dashboard) {
        this.resizeSubscription = this.dashboard.onChangeDashboard.subscribe(() => {
          this.ng1Scope.$broadcast('dashboardResize');
        });
      }
    }
  }

  ngOnDestroy(): void {
    if (this.ng1Scope) {
      this.ng1Scope.$destroy();
    }
    if (this.resizeSubscription) {
      this.resizeSubscription.unsubscribe();
    }
  }
}
